import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Food } from 'src/app/client1/models/food.model';
import { FoodAdminService } from 'src/app/client1/services/food-admin.service';
import {CommandService} from '../../../services/command.service';
import {AlertController} from '@ionic/angular';
import {CommandAutoAdminService} from '../../../services/commandAutoAdmin.service';
import {CommandsToPrepareService} from '../../../services/commandsToPrepare.service';
import {CommandsToPrepareModel} from '../../../models/commandsToPrepare.model';
import {CanalService} from '../../../services/canal.service';
import {SharedService} from '../../../services/Shared.service';

@Component({
  selector: 'app-listing',
  templateUrl: './listing-command-page.component.html',
  styleUrls: ['./listing-command-page.component.scss'],
  //providers: [SharedDataService], // Ajoutez le service ici

})
export class ListingCommandPage implements OnInit {
  getAllCommandesReceived: CommandsToPrepareModel[] | null = null;
  private notificationSound = new Audio('assets/audio/songOrder.mp3');
  // eslint-disable-next-line @typescript-eslint/member-ordering
  userId: any;
  // eslint-disable-next-line @typescript-eslint/member-ordering
  loading = false; // Variable pour gérer l'affichage de l'indicateur de chargement

  constructor(
    private commandsToPrepareService: CommandsToPrepareService,
    private alertCtrl: AlertController,
    private canalService: CanalService,
    private sharedService: SharedService,
  ) {
  }

  ngOnInit(): void {
    this.getParamsURL();
    this.getData(this.canalService.getUserIdFoCommand());
  }

  getParamsURL(){
    // Obtenir l'URL complète
    const url = window.location.href;
// Obtenir les paramètres de l'URL
    const params = new URLSearchParams(new URL(url).search);
    this.userId = params.get('user');
    console.log('valeur de userIdCommand : '+this.userId); // Affiche 'valeur'
    this.canalService.setUserIdFoCommand(this.userId);
  }

  getData(userId: number) {
    this.commandsToPrepareService.allCommandsByUserId(userId).subscribe(
      (data) => {
        this.getAllCommandesReceived = data; // Mettez à jour les données dès qu'elles sont disponibles ou mises à jour
        this.updateSharedVariable(this.getAllCommandesReceived.length);
      },
      (error) => {
        console.error('Erreur lors de la récupération des données:', error);
      }
    );
  }

  async deleteThisOrder(id: number) {
    const alert = await this.alertCtrl.create({
      header: 'Remove',
      message: 'Vous avez traité cette commande?',
      buttons: [
        {
          text: 'No',
        },
        {
          text: 'Yes',
          handler: () => {
            this.deleteCommande(id);
          }
        },
      ],
    });

    alert.present();
  }

  deleteCommande(commandeId: number) {
    this.commandsToPrepareService.deleteCommand(commandeId).subscribe(
      () => {
        //this.getData(this.canalService.getUserId()); // Mettez à jour les données après la suppression de la commande
        //console.log('Commande supprimée avec succès !');
      },
      (error) => {
        console.error('Erreur lors de la suppression de la commande:', error);
      }
    );
    this.showLoadingAlert();
  }

  updateSharedVariable(newValue: number): void {
    this.sharedService.updateCommandToPreparLength(newValue);
  }



  async showLoadingAlert() {
    const alert = await this.alertCtrl.create({
      message: '<div class="centered-alert"><ion-spinner name="crescent"></ion-spinner></div>',
      backdropDismiss: false, // Empêche la fermeture de l'alerte en cliquant en dehors
    });

    await alert.present();

    // Simulez une opération asynchrone (par exemple, une requête HTTP)
    setTimeout(() => {
      alert.dismiss(); // Fermez l'alerte après la fin de l'opération
    }, 3000); // Simulez une opération de 3 secondes (remplacez par votre logique réelle)
  }

}
